@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?s3ieyq');
  src:  url('../fonts/icomoon.eot?s3ieyq#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?s3ieyq') format('truetype'),
    url('../fonts/icomoon.woff?s3ieyq') format('woff'),
    url('../fonts/icomoon.svg?s3ieyq#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.unselectable {
   -moz-user-select: -moz-none;
   -khtml-user-select: none;
   -webkit-user-select: none;

   /*
     Introduced in IE 10.
     See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
   */
   -ms-user-select: none;
   user-select: none;
}

.dragPanelContent {     
    position: relative;
    width: 900px;
}

.eyui-drag {
    display: inline-block;    
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 9999;
    min-width: 280px;
    width: 280px;
    box-shadow: 0 0 6px rgba(0, 33, 50, 0.1), 0 7px 25px rgba(17, 38, 60, 0.4);
}

.eyui-drag .eyui-drag-header {
    background: #828282;
    color: #fff;
    min-height: 26px;
    line-height: 26px;
    padding: 3px;
    overflow: hidden;
    cursor: move;
}

.eyui-drag .eyui-drag-header .eyui-drag-title {
    float: left;
    width: calc(100% - 90px);
}

.eyui-drag .eyui-drag-header .eyui-drag-tools {
    font-family: 'icomoon' !important;
    float: right;
}

.eyui-drag .eyui-drag-header .eyui-drag-tools i {
    cursor: pointer;
}

.eyui-drag .eyui-drag-header .eyui-drag-tools .closeBtn:before {
    content: "\ea0f";
}

.eyui-drag .eyui-drag-header .eyui-drag-tools .upBtn:before {
    content: "\ea41";
}

.eyui-drag .eyui-drag-header .eyui-drag-tools .downBtn:before {
    content: "\ea43";
}

.eyui-drag .eyui-drag-content {
    min-height: 150px;
    background: #fff;    
    padding: 10px;
    overflow-x: hidden;
}

.eyui-left-menu{
    cursor: pointer;
    margin: 0;
    padding: 0;
    outline: none;
    width: 210px;
}

.eyui-left-menu .eyui-left-menu-list > ul {
    border: 1px solid #dad8d8;
    border-radius: 4px;
    padding-left: 0;
    margin-bottom: 20px;
}
.eyui-left-menu ul {
    list-style: none;
    padding-left: 20px;
    cursor: pointer;
}
.eyui-left-menu ul li {
    position: relative;
    border-bottom: 1px solid #dad8d8;    
    line-height: 36px;
    min-width: 140px;
}
.eyui-left-menu ul li a {    
    display: block;
    padding: 0 20px;    
    color: #222;
    text-decoration: none;
}
.eyui-left-menu ul li a:hover{
    color: #55a3e6;
}
.eyui-left-menu li:last-child {
    border-bottom: none;
}
.eyui-left-menu li > ul {
    border-top: 1px solid #dad8d8;
}
.eyui-left-menu li > ul > li {
    float: none;
    position: relative;
    min-width: 140px;
}
.eyui-left-menu ul > li i.collapsed  {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 14px;
    padding-right: 6px;
}
.eyui-left-menu ul > li ul.collapsed  {
    display: none;
}
.eyui-left-menu .selected{
    border-right: 2px solid #3F51B5;
    color: #55a3e6;
}

.eyui-slide-follow {width: 200px;height: 200px;overflow: hidden;border:1px solid #dcdcdc;}
.eyui-slide-follow .slide li {height: 49px;line-height: 49px;text-align: left;padding: 0 10px;font-size: 16px;list-style: none;border-bottom: 1px dashed #dcdcdc;cursor: pointer;}
.eyui-slide-follow .slide li:hover{background: #ccc;}

.eyui-croll-orizontal{white-space: nowrap; cursor:hover; overflow: hidden;width: 100%;}
.eyui-croll-orizontal .inner{     display: inline-block;overflow:hidden;}
.eyui-croll-orizontal .inner p{ display:inline-block;}
.eyui-tabs{    
    width: 100%;
    min-width: 357px;
    font-size: 14px;
    color: #333;
}
.eyui-tabs-head {
    padding-bottom: 5px;
    height: 30px;
    background: url(../images/tabbg.gif) repeat-x;
    border-left: solid 1px #ddd;
    border-right: solid 1px #ddd;
    cursor: pointer;
}

.eyui-tabs-item {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
}

.eyui-tabs-item-active {
    border-left: solid 1px #d5d5d5;
    border-right: solid 1px #d5d5d5;
    border-top: solid 2px #ff6600;
    height: 29px;
    line-height: 29px;
    background: #fff;
    color: #3366cc;
    font-weight: 800;
}
.eyui-tabs-item-active:first-child{
    border-left: solid 0px;
}

.eyui-tabs-detail{
    border-width: 0 1px 1px 1px;
    border-color: #ddd;
    border-style: solid;
    position: relative;
    min-height: 180px;
    overflow: hidden;
}
